import React, { Component } from 'react';
import project1 from '../img/project1.png';
import project2 from '../img/project2.png';
import project3 from '../img/project3.png';
import project4 from '../img/project4.png';

class Portfolio extends Component {
    render() {
        return (
            <div className="condiv">
            <h1 className="subtopic">My Work</h1>
            <div className="hr_line line2 glass"><div className="inner2 inner3"></div></div>
                <div class="port_out port1 glass">
                    <a href="https://github.com/ahtrag/simplecrypto">
                        <img alt="port-1" src={project1} className="po1"/>
                    </a>
                </div>
                <div class="port_out port2 glass">
                    <a href="https://github.com/ahtrag/oribox">
                        <img alt="port-2" src={project2} className="po1"/>
                    </a>
                </div>
                <div class="port_out port3 glass">
                    <a href="https://github.com/ahtrag/simplepokedex">
                        <img alt="port-3" src={project3} className="po1"/>
                    </a>
                </div>
                <div class="port_out port4 glass">
                    <a href="https://github.com/ahtrag/simplecalc">
                        <img alt="port-4" src={project4} className="po1"/>
                    </a>
                </div>
            </div>
            )
        }
    }
    
export default Portfolio
    