import React, { Component } from 'react';
import ReactTypingEffect from 'react-typing-effect';
import Social from '../components/Social'



class Home extends Component {
    render() {
        return (
            <div className="condiv home">
            <h1>Hello, I'm Gartha Prasidhiyanta</h1>
            <ReactTypingEffect
                className="typingeffect"
                text={['Front-End Developer','Gamer','Bookworm','Morning Person']}
                speed={100}
                eraseDelay={900}
            />
            <div className="hr_line glass"><div className="hr_inner"></div></div>
            
            <div className="summary">
            Front-End Developer who have proficiency to create server-less web using
            React JS and Firebase. Making a self-made startup with my team called&nbsp;
            <a target="_blank"  rel="noopener noreferrer" href="https://www.childminder.id/">childminder.id</a>&nbsp;as an output 
            of&nbsp;<a href="https://1000startupdigital.id">1000Startup Digital</a>&nbsp;events.
            Passionate on learning new things on daily basis
            </div>

            <Social />
            </div>
            )
        }
    }
    
    export default Home
    